<template>
    <div class="main-wrapper">
      <img src="../assets/restro-logo.png" />
      <h1>Login</h1>
      <div class="form-input">
        <div>
          <label for="email">Email:</label>
          <input type="email" id="email" v-model="user.email" required /> <!-- to bind we are using v-modal using the fuction we made below named SignUpForm-->
        </div>
        <div>
          <label for="password">Password:</label>
          <input type="password" id="password" v-model="user.password" required />
        </div>
        <button type="submit" v-on:click="LoginData">Login</button>
        <p>Don't have an account? <router-link to="/signup">Register</router-link></p>
        <p><router-link to="/forgot-password">Forgot Password</router-link></p>
      </div>
    </div>
  </template>
  <script>
  export default {
    name: "LoginPage",
    // methods: {
    //   defining our functions into methods
    //   LoginData() {
    //     console.log(this.LoginForm.name, this.LoginForm.email, this.LoginForm.password);
    //   },
    // },
    // data() {
    //     defining properties
    //     return {
    //       LoginForm:{
    //         object name
    //         name:null, using name word from the v-modal to get the input data entered
    //         email:null,
    //         password:null,
    //         error:[],
    //       }}
    //   },

    data() {
    return {
      user: {
        email: '',
        password: '',
      },
    };
  },
  computed: {
    isValidEmail() {
      // Simple email validation check
      return /^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(this.user.email)
    },
    isValidPassword() {
      // Simple password validation check
      return this.user.password.length >= 8;
    }
  },
  methods: {
    LoginData() {
      const { email, password } = this.user;
      const users = JSON.parse(localStorage.getItem('users')) || [];
      const user = users.find(u => u.email === email && u.password === password);
      if (user) {
        // user found in local storage, redirect to home page
        this.$router.push('/home');
      } else {
        // user not found, redirect to register page
        // this.$router.push('/register');
        alert('Invalid login credentials');
      }
    },
  },
      
  };
  </script>
