<template>
  <div class="main-wrapper">
    <h2>Profile</h2>
    <div class="profile-info">
      
      <div class="profile-details" v-for="(userDetails, index) in users" :key="index">
        <template v-if="userDetails">
        <p>Name: {{ userDetails.name }}</p>
        <p>Email: {{ userDetails.email }}</p>
      </template>
      </div>
    </div>
  </div>
</template>
  
  <script>
export default {
  name: "ProfilePage",
  data() {
    return {
      users: JSON.parse(localStorage.getItem("users")),
    };
  }
};
</script>
  