<template>
    <div class="main-wrapper">
      <h2>Forgot Password</h2>
      <form @submit.prevent="submitForm">
        <div>
          <label for="email">Email:</label>
          <input type="email" id="email" v-model="email" required>
          <p v-if="email && !isValidEmail">Please enter a valid email address.</p>
        </div>
        <div>
          <button type="submit" :disabled="!email || !isValidEmail">Submit</button>
          <p><router-link to="/">Login</router-link></p>
        </div>
      </form>
    </div>
  </template>
  
  <script>
  export default {
    data() {
      return {
        email: '',
      }
    },
    computed: {
      isValidEmail() {
        // Simple email validation check
        return /^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(this.email)
      },
    },
    methods: {
      submitForm() {
        // Send email to user with password reset instructions
        alert('Password reset email sent')
      },
    },
  }
  </script>
  