import Vue from 'vue'
import VueRouter from 'vue-router'

import HomePage from '@/component/HomePage.vue'
import SignUp from '@/component/SignUp.vue'
import LoginPage from '@/component/LoginPage.vue'
import ForgotPassword from '@/component/ForgotPassword.vue'
import ProfilePage from '@/component/ProfilePage.vue'


Vue.use(VueRouter)

const routes = [
    
    {
        name: 'LoginPage',
        component: LoginPage,
        path: '/',
    },
    {
        name: 'SignUp',
        component: SignUp,
        path: '/signup',
    },
    {
        name: 'HomePage',
        component: HomePage,
        path: '/home',
    },
    {
        name: 'ForgotPassword',
        component: ForgotPassword,
        path: '/forgot-password',
    },
    {
        name: 'ProfilePage',
        component: ProfilePage,
        path: '/profile',
    }
];


const router = new VueRouter({
    routes
  })
  
  export default router