<template>
  <div class="main-wrapper">
    <img src="../assets/restro-logo.png" />
    <h2>Register</h2>
    <div class="form-input">
      <div>
        <label for="name">Name:</label>
        <input type="text" id="name" v-model="user.name" required />
      </div>
      <div>
        <label for="email">Email:</label>
        <input type="email" id="email" v-model="user.email" required pattern="[^\s@]+@[^\s@]+\.[^\s@]+" />
        
        <!-- to bind we are using v-modal using the fuction we made below named SignUpForm-->
      </div>
      <div>
        <label for="password">Password:</label>
        <input type="password" id="password" v-model="user.password" required />
      </div>
      <button type="submit" v-on:click="RegisterUser">Login</button>
      <p><router-link to="/">Login</router-link></p>
    </div>
  </div>
</template>

<script>
export default {
    name: "RegisterPage",
//     data() {
//     return {
//       name: '',
//       email: '',
//       password: ''
//     }
//   },
//   mounted() {
//      Retrieve form data from local storage
//     this.name = localStorage.getItem('name');
//     this.email = localStorage.getItem('email');
//     this.password = localStorage.getItem('password');
//   },
//   methods: {
//     RegisterUser() {
//        Handle form submission
//       localStorage.setItem('name', this.name);
//       localStorage.setItem('email', this.email);
//       localStorage.setItem('password', this.password);
//     }
//   }

data() {
    return {
      user: {
        name: '',
        email: '',
        password: '',
        id:''
      },
    };
  },
  computed: {
    isValidEmail() {
      // Simple email validation check
      return /^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(this.user.email)
    },
    isValidPassword() {
      // Simple password validation check
      return this.user.password.length >= 8;
    }
  },
  methods: {
    RegisterUser() {
      if (!this.isValidEmail) {
        alert('Please enter a valid email address.')
        return
      }
      if (!this.isValidPassword) {
        alert('Please enter a valid password.')
        return
      }
      const { name, email, password } = this.user;
      const users = JSON.parse(localStorage.getItem('users')) || [];
      users.push({ name, email, password });
      localStorage.setItem('users', JSON.stringify(users));
      this.user.name = '';
      this.user.email = '';
      this.user.password = '';
      this.id
    },
  },
};
</script>